}

.switch {
  width: 30px;
  height: 16px;
  display: inline-block;
  position: absolute;
  top: 50%;
  right: 2px;
  transform: translateY(-50%);
}

.switch input {
  display: none;
}

.slider {
  cursor: pointer;
  background-color: #ccc;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 9px;
  width: 9px;
  background-color: var(--bg-color);
  transition: all .4s;
  position: absolute;
  bottom: 4px;
  left: 4px;
}

input:checked + .slider {
  background-color: rgba(0, 0, 0, 0);
}

input:focus + .slider {
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
}

input:checked + .slider:before {
  transform: translateX(14px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

